:root {
  --bg: #0b0e13;
  --card: #121826;
  --border: #1f2937;
  --text: #e6eefc;
  --muted: #a9b6cc;
  --accent: #3ea1ff;
  --accent-2: #7c4dff;
  --danger: #ff4d7a;
}

.page {
  min-height: 100dvh;
  background: radial-gradient(1200px 600px at 20% -10%, rgba(62,161,255,0.12), transparent 60%),
              radial-gradient(900px 500px at 110% 0%, rgba(124,77,255,0.10), transparent 55%),
              var(--bg);
  color: var(--text);
}

.header {
  border-bottom: 1px solid var(--border);
  background: linear-gradient(180deg, rgba(62,161,255,0.12), rgba(124,77,255,0.10) 60%, transparent);
}
.headerInner {
  max-width: 1000px;
  margin: 0 auto;
  padding: 28px 16px;
}
.title {
  margin: 0;
  font-size: 1.8rem;
  letter-spacing: 0.2px;
}
.subtitle {
  margin: 6px 0 0 0;
  color: var(--muted);
}

.container {
  max-width: 1000px;
  margin: 20px auto;
  padding: 0 16px 40px;
  display: grid;
  gap: 16px;
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 16px;
  box-shadow: 0 10px 24px rgba(0,0,0,0.25);
}

.summaryTop {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 16px;
  align-items: center;
}
.posterStub {
  width: 120px;
  height: 168px;
  border-radius: 12px;
  background:
    linear-gradient(135deg, rgba(62,161,255,0.18), rgba(124,77,255,0.18));
  border: 1px solid var(--border);
}
.summaryText { display: grid; gap: 8px; }
.movieTitle { margin: 0; font-size: 1.25rem; }
.metaRow { display: flex; gap: 10px; }
.metaLabel { width: 110px; color: var(--muted); }
.metaValue { font-weight: 600; }

.sectionTitle { margin: 0 0 12px 0; font-size: 1.1rem; }

.form { display: grid; gap: 12px; }
.grid {
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
@media (max-width: 700px) {
  .grid { grid-template-columns: 1fr; }
  .summaryTop { grid-template-columns: 1fr; }
  .posterStub { width: 100%; height: 220px; }
}

.field { display: grid; gap: 6px; }
.fieldCheckbox { align-content: end; }

label { font-weight: 600; }
.hint { color: var(--muted); }

input[type="text"],
input[type="email"],
input[type="number"] {
  background: #0b101a;
  color: var(--text);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 10px 12px;
  outline: none;
}
input:focus { border-color: var(--accent); box-shadow: 0 0 0 3px rgba(62,161,255,0.18); }

.checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  background: #0b101a;
  border: 1px solid var(--border);
  border-radius: 10px;
}

.actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-top: 8px;
}

.primary,
.secondary {
  padding: 10px 14px;
  border-radius: 10px;
  font-weight: 700;
  letter-spacing: 0.2px;
  cursor: pointer;
  transition: transform 0.06s ease, box-shadow 0.2s ease, background 0.2s ease, border-color 0.2s ease;
}

.primary {
  background: linear-gradient(180deg, var(--accent), #2b70ff);
  color: white;
  border: 1px solid #2a60d9;
  box-shadow: 0 8px 20px rgba(62,161,255,0.35);
}
.primary:disabled {
  background: #2a3450;
  border-color: #2a3450;
  color: #a5b3cc;
  box-shadow: none;
  cursor: not-allowed;
}
.primary:not(:disabled):active { transform: translateY(1px); }

.secondary {
  background: transparent;
  color: #cfe1ff;
  border: 1px solid #30405a;
}
.secondary:hover { background: #0e1422; }
.secondary:active { transform: translateY(1px); }